'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import Image from 'next/image';
import Link from 'next/link';
import { useParams, useRouter } from 'next/navigation';
import { toast } from 'sonner';
import { 
    Star, Minus, Plus, ShoppingCart, ArrowLeft, CheckCircle2, Clock, User, Receipt, Utensils
} from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';

import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input'; 
import { Textarea } from '@/components/ui/textarea'; 
import { Separator } from '@/components/ui/separator';
import { ProdukResto } from '@/types';

export default function ProductDetailPage() {
    const params = useParams();
    const router = useRouter();
    const id = params.id;
    const { user } = useAuth({ middleware: 'auth' });

    const [product, setProduct] = useState<ProdukResto | null>(null);
    const [relatedProducts, setRelatedProducts] = useState<ProdukResto[]>([]);
    const [loading, setLoading] = useState(true);
    
    // State Transaksi
    const [quantity, setQuantity] = useState(1);
    const [catatan, setCatatan] = useState(''); 
    const [tableNumber, setTableNumber] = useState(''); 
    const [isProcessing, setIsProcessing] = useState(false);

    // Fetch Data
    useEffect(() => {
        const fetchData = async () => {
            if (!id) return;
            try {
                setLoading(true);
                // 1. Ambil Detail Produk
                const resDetail = await axios.get(`/api/produkRestos/${id}`);
                setProduct(resDetail.data);

                // 2. Ambil Produk Lain untuk rekomendasi
                const resAll = await axios.get('/api/produkRestos');
                const others = resAll.data
                    .filter((p: ProdukResto) => p.id !== Number(id) && p.is_tersedia)
                    .slice(0, 3); // Ambil 3 rekomendasi
                setRelatedProducts(others);

            } catch (error) {
                console.error(error);
                toast.error("Produk tidak ditemukan.");
                router.push('/pelanggan/produk');
            } finally {
                setLoading(false);
            }
        };
        fetchData();
    }, [id, router]);

    const handleIncrement = () => setQuantity(prev => prev + 1);
    const handleDecrement = () => setQuantity(prev => (prev > 1 ? prev - 1 : 1));

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);

    // Handle Order Langsung
    const handleOrder = async () => {
        if (!tableNumber) {
            toast.error("Mohon isi Nomor Meja terlebih dahulu!");
            return;
        }

        setIsProcessing(true);
        try {
            // Payload Transaksi Langsung
            const payload = {
                payment_method_id: 1, // Default Cash / Pay Later
                total_bayar: 0, 
                nama_pelanggan: `${user?.name} (Meja ${tableNumber})`,
                items: [
                    {
                        produk_resto_id: product?.id,
                        jumlah: quantity
                        // Catatan bisa ditambahkan jika backend mendukung detail catatan per item
                        // atau digabung ke nama pelanggan jika backend terbatas
                    }
                ]
            };

            await axios.post('/api/transaksiRestos', payload);

            toast.success("Pesanan Berhasil Dibuat!", {
                description: `${quantity}x ${product?.nama_produk} akan segera disiapkan.`,
                icon: <CheckCircle2 className="text-green-600" />
            });
            
            // Redirect ke menu atau history
            setTimeout(() => router.push('/pelanggan/produk'), 1500);

        } catch (error: any) {
            console.error(error);
            toast.error("Gagal membuat pesanan.");
        } finally {
            setIsProcessing(false);
        }
    };

    if (loading) return (
        <div className="min-h-screen bg-white flex items-center justify-center">
            <div className="animate-pulse flex flex-col items-center">
                <div className="h-12 w-12 bg-gray-200 rounded-full mb-4"></div>
                <div className="text-gray-400 font-medium">Memuat menu lezat...</div>
            </div>
        </div>
    );
    
    if (!product) return null;

    const totalPrice = Number(product.harga_jual) * quantity;

    return (
        <div className="min-h-screen bg-[#f8f9fa] font-sans text-gray-900">
            <main className="max-w-7xl mx-auto px-4 md:px-6 py-8 md:py-12">
                
                {/* Breadcrumb */}
                <div className="mb-6">
                    <Link href="/pelanggan/produk" className="inline-flex items-center text-gray-500 hover:text-[#D93F21] transition-colors font-medium group mb-4">
                        <div className="w-8 h-8 rounded-full bg-white border border-gray-200 flex items-center justify-center mr-3 group-hover:bg-[#D93F21] group-hover:text-white group-hover:border-[#D93F21] transition-all">
                             <ArrowLeft className="w-4 h-4" /> 
                        </div>
                        Kembali ke Menu
                    </Link>
                </div>

                <div className="flex flex-col lg:flex-row gap-8 items-start relative">
                    
                    {/* === BAGIAN KIRI: DETAIL VISUAL & INFO (65%) === */}
                    <div className="w-full lg:w-2/3 space-y-10">
                        
                        {/* 1. Foto & Judul */}
                        <div className="bg-white rounded-[2.5rem] p-4 md:p-6 border border-gray-100 shadow-sm">
                            <div className="relative aspect-video w-full rounded-[2rem] overflow-hidden bg-gray-100 mb-6 group">
                                <Image 
                                    src={product.foto_url || '/images/placeholder-food.jpg'} 
                                    alt={product.nama_produk}
                                    fill
                                    className="object-cover transition-transform duration-700 group-hover:scale-105"
                                    unoptimized
                                />
                                <div className="absolute top-4 left-4 flex gap-2">
                                    <Badge variant="secondary" className="bg-white/90 backdrop-blur text-gray-800 shadow-sm px-3 py-1">
                                        {product.kategori?.nama_kategori}
                                    </Badge>
                                </div>
                            </div>

                            <div className="px-2">
                                <h1 className="text-3xl md:text-4xl font-extrabold text-gray-900 mb-3 leading-tight">
                                    {product.nama_produk}
                                </h1>
                                <div className="flex items-center gap-4 mb-6">
                                    <div className="flex items-center gap-1">
                                        <Star className="w-5 h-5 text-yellow-400 fill-yellow-400" />
                                        <span className="font-bold text-gray-900">4.8</span>
                                        <span className="text-gray-400 text-sm">(120+ Reviews)</span>
                                    </div>
                                    <div className="h-4 w-px bg-gray-200"></div>
                                    <div className="flex items-center gap-2 text-sm text-gray-500">
                                        <Clock className="w-4 h-4" /> 15-20 Mins
                                    </div>
                                </div>
                                
                                <p className="text-gray-600 text-lg leading-relaxed">
                                    {product.deskripsi || "Menu spesial dengan cita rasa otentik khas MainDiHatta. Dibuat dari bahan-bahan segar pilihan dan disajikan dengan penuh cinta."}
                                </p>
                            </div>
                        </div>

                        {/* 2. Rekomendasi */}
                        <div>
                            <h3 className="text-xl font-bold text-gray-900 mb-6 flex items-center gap-2">
                                <Utensils className="w-5 h-5 text-[#D93F21]" /> Menu Lainnya
                            </h3>
                            <div className="grid grid-cols-1 sm:grid-cols-3 gap-5">
                                {relatedProducts.map((item) => (
                                    <Link key={item.id} href={`/pelanggan/produk/${item.id}`}>
                                        <div className="group bg-white rounded-2xl p-3 border border-gray-100 hover:border-[#D93F21]/30 hover:shadow-lg transition-all cursor-pointer h-full flex flex-col">
                                            <div className="relative h-32 w-full rounded-xl overflow-hidden mb-3 bg-gray-100">
                                                <Image 
                                                    src={item.foto_url || '/images/placeholder-food.jpg'} 
                                                    alt={item.nama_produk}
                                                    fill
                                                    className="object-cover group-hover:scale-110 transition-transform duration-500"
                                                    unoptimized
                                                />
                                            </div>
                                            <h4 className="font-bold text-gray-900 text-sm line-clamp-1 group-hover:text-[#D93F21] transition-colors px-1">
                                                {item.nama_produk}
                                            </h4>
                                            <p className="text-xs text-gray-500 px-1 mt-1">{formatRupiah(Number(item.harga_jual))}</p>
                                        </div>
                                    </Link>
                                ))}
                            </div>
                        </div>
                    </div>

                    {/* === BAGIAN KANAN: SIDEBAR ORDER (35%) - STICKY === */}
                    <div className="w-full lg:w-1/3">
                        <div className="sticky top-24 bg-white rounded-[2rem] shadow-xl border border-gray-100 overflow-hidden flex flex-col">
                            
                            {/* Header Sidebar */}
                            <div className="p-6 border-b border-gray-100 bg-gray-50/50">
                                <h3 className="text-[#D93F21] font-bold text-lg mb-4 flex items-center gap-2">
                                    <Receipt className="w-5 h-5" /> Detail Pesanan
                                </h3>
                                
                                {/* Customer Info */}
                                <div className="space-y-3 bg-white p-4 rounded-xl border border-gray-100 shadow-sm">
                                    <div>
                                        <p className="text-xs text-gray-400 font-medium mb-1">Pelanggan</p>
                                        <div className="flex items-center gap-2 font-bold text-gray-900 text-sm">
                                            <User className="w-4 h-4 text-gray-400" />
                                            {user?.name || 'Guest'}
                                        </div>
                                    </div>
                                    <Separator className="my-2" />
                                    <div>
                                        <p className="text-xs text-gray-400 font-medium mb-1">Nomor Meja</p>
                                        <Input 
                                            type="number" 
                                            placeholder="Contoh: 12" 
                                            className="font-bold border-gray-200 focus:border-[#D93F21] rounded-lg h-9 text-sm"
                                            value={tableNumber}
                                            onChange={(e) => setTableNumber(e.target.value)}
                                        />
                                    </div>
                                </div>
                            </div>

                            {/* Order Config */}
                            <div className="p-6 space-y-6">
                                {/* Quantity Control */}
                                <div>
                                    <label className="text-sm font-bold text-gray-900 mb-3 block">Jumlah Porsi</label>
                                    <div className="flex items-center justify-between bg-gray-50 p-2 rounded-xl border border-gray-200">
                                        <button 
                                            onClick={handleDecrement}
                                            className="w-10 h-10 rounded-lg bg-white border border-gray-200 flex items-center justify-center hover:bg-[#D93F21] hover:text-white hover:border-[#D93F21] transition-all shadow-sm"
                                        >
                                            <Minus className="w-4 h-4" />
                                        </button>
                                        <span className="text-xl font-bold text-gray-900 w-12 text-center">{quantity}</span>
                                        <button 
                                            onClick={handleIncrement}
                                            className="w-10 h-10 rounded-lg bg-white border border-gray-200 flex items-center justify-center hover:bg-[#D93F21] hover:text-white hover:border-[#D93F21] transition-all shadow-sm"
                                        >
                                            <Plus className="w-4 h-4" />
                                        </button>
                                    </div>
                                </div>

                                {/* Catatan */}
                                <div>
                                    <label className="text-sm font-bold text-gray-900 mb-2 block">Catatan Masakan</label>
                                    <Textarea 
                                        placeholder="Cth: Pedas, Jangan pakai bawang..." 
                                        className="bg-gray-50 border-gray-200 focus:border-[#D93F21] rounded-xl resize-none h-24 text-sm"
                                        value={catatan}
                                        onChange={(e) => setCatatan(e.target.value)}
                                    />
                                </div>
                            </div>

                            {/* Footer Payment */}
                            <div className="p-6 bg-gray-900 text-white mt-auto">
                                <div className="flex justify-between items-center mb-6">
                                    <span className="text-gray-400 text-sm">Total Harga</span>
                                    <span className="text-2xl font-bold text-[#D93F21]">{formatRupiah(totalPrice)}</span>
                                </div>

                                {product.is_tersedia ? (
                                    <Button 
                                        onClick={handleOrder}
                                        disabled={isProcessing}
                                        className="w-full bg-[#D93F21] hover:bg-[#b9351b] text-white font-bold py-6 rounded-xl text-lg shadow-lg shadow-orange-900/20 transition-all hover:scale-[1.02] active:scale-95"
                                    >
                                        {isProcessing ? "Memproses..." : "Pesan Sekarang"}
                                    </Button>
                                ) : (
                                    <Button disabled className="w-full bg-gray-700 text-gray-400 font-bold py-6 rounded-xl text-lg cursor-not-allowed">
                                        Stok Habis
                                    </Button>
                                )}
                            </div>

                        </div>
                    </div>

                </div>
            </main>
        </div>
    );
}